	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.moveabletype.org\">Moveable Type 2.0</a> has been released. Time to update <a href=\"http://www.papascott.de\">papascott.de</a> and start posting there again. I'd also like to somehow convert the happy-stripe design here to a CSS design for MT. Good thing the Easter holidays are coming up.</p>\n<p>I've stayed in FreeBSD since Monday. My system is usable, but not yet complete. My problems so far have been the java plugin for Mozilla (crashes on the Domino mail applet) and finding a Citrix client (Linux version runs under emulation, but doesn't encrypt so I can't log in).</p>","fields":{"title":"Dog Food","date":"21 Mar 2002","url":"https://papascott.de/2002-03-21-dog-food/"}}},"pathContext":{"slug":"/2002-03-21-dog-food/","prev":{"fields":{"title":"Dog Food","date":"18 Mar 2002","slug":"/2002-03-18-dog-food/"}},"next":{"fields":{"title":"Resuming Transmission","date":"21 Mar 2002","slug":"/2002-03-21-resuming-transmission/"}}}}

